.label {
  @apply mb-1 font-semibold text-sm text-gray-400;
}
.input,
.textarea {
  @apply border-2 border-gray-700 hover:border-gray-500 
  py-2 px-3 rounded-lg bg-gray-800 text-gray-400 outline-none;
}

.input:focus,
.textarea:focus {
  @apply border-2 border-secondary-500 shadow-gray-800 shadow-md;
}

.inputContainer {
  @apply flex flex-col mb-4 m-2 flex-grow;
  min-width: 420px;
}

.button {
  @apply block bg-primary-500 hover:bg-primary-600 text-white uppercase text-lg mx-auto p-4 rounded;
}

.error {
  @apply text-red-400 mt-1;
}
