.container {
  @apply flex flex-col relative w-full h-full;
}

.tabsContainer {
  @apply flex;
}

.tab {
  @apply p-4 text-center text-lg
   text-gray-500 w-1/2 cursor-pointer border-b-2 border-gray-600 relative outline-none box-content rounded-3xl border-t-8 border-t-transparent shadow-lg transition-all duration-300 max-w-xs;
}

.tab:hover {
  @apply text-gray-400 border-t-primary-600 border-opacity-25  border-b-transparent shadow-none;
}

.activeTab {
  @apply text-gray-300 text-lg border-b-transparent border-t-primary-600 hover:border-t-primary-600 shadow-none;
}
.contentContainer {
  flex-grow: 1;
}

.content {
  @apply p-5 w-full h-full;
  display: none;
}

.activeContent {
  @apply block;
}
