.topCardsContainer {
  @apply flex overflow-auto;
}

.topCard {
  @apply grow w-56 h-28 m-2
    shadow-lg rounded-lg overflow-hidden
    flex items-stretch justify-between;
  min-width: 300px;
  max-width: 400px;
}

.topCardIcon {
  @apply flex items-center justify-center;
  min-width: 80px;
  max-width: 80px;
}

.topCardContent {
  @apply flex flex-col h-full justify-around grow px-2;
}

.topCardTitle {
  @apply text-xl text-gray-400 opacity-90 overflow-clip;
}

.topCardSubtitle {
  @apply text-base text-gray-400 opacity-70;
}

.topCard:nth-child(1) {
  @apply bg-combos-1-light;
}

.topCard:nth-child(1) .topCardIcon {
  @apply bg-combos-1-dark;
}

.topCard:nth-child(2) {
  @apply bg-combos-2-light;
}

.topCard:nth-child(2) .topCardIcon {
  @apply bg-combos-2-dark;
}

.topCard:nth-child(3) {
  @apply bg-combos-3-light;
}

.topCard:nth-child(3) .topCardIcon {
  @apply bg-combos-3-dark;
}

.topCard:nth-child(4) {
  @apply bg-combos-4-light;
}

.topCard:nth-child(4) .topCardIcon {
  @apply bg-combos-4-dark;
}

