.container {
  @apply flex flex-row mb-4 m-2 gap-3 hover:cursor-pointer;
  min-width: 420px;
}
.toggle.react-toggle--checked .react-toggle-track {
  @apply bg-secondary-700;
}

.react-toggle--focus .react-toggle-thumb {
  -webkit-box-shadow: 0px 0px 3px 2px #71ad43;
  -moz-box-shadow: 0px 0px 3px 2px #71ad43;
  box-shadow: 0px 0px 2px 3px #71ad43;
}

.react-toggle:active:not(.react-toggle--disabled) .react-toggle-thumb {
  -webkit-box-shadow: 0px 0px 5px 5px #71ad43;
  -moz-box-shadow: 0px 0px 5px 5px #71ad43;
  box-shadow: 0px 0px 5px 5px #71ad43;
}
