/**
***
*** react-select
***
**/

.react-select-container .react-select__control {
  @apply bg-gray-800 border-2 border-gray-700 hover:border-gray-500  
  py-1 px-3 rounded-lg;
}

.react-select-container .react-select__control--is-focused {
  @apply hover:border-secondary-500
  border-2 border-secondary-500 shadow-gray-800 shadow-md;
}

.react-select-container .react-select__menu {
  @apply bg-gray-800 border-2  border-gray-600;
}

.react-select-container .react-select__option {
  @apply text-gray-200 bg-gray-800 hover:bg-gray-900;
}
.react-select-container .react-select__option--is-focused {
  @apply bg-gray-900;
}

.react-select-container .react-select__dropdown-indicator {
  @apply text-gray-500 hover:text-gray-500;
}

.react-select-container .react-select__clear-indicator {
  @apply text-gray-500 hover:text-gray-400;
}

.react-select-container .react-select__single-value {
  @apply text-neutral-200;
}

.react-select-container .react-select__input-container,
.react-select-container .react-select__placeholder {
  @apply text-neutral-200;
}

.react-select-container .react-select__indicator-separator {
  @apply bg-gray-500;
}
.react-select-container .react-select__indicator-separator {
  @apply bg-gray-500;
}
