/* TODO: compoine in shared menu class */
.menuPinned {
  @apply z-sideBarMenu w-80 h-screen m-0 bg-gray-800  shadow-2xl
  /* TODO: same as icons menu issue.. y scroll converts x to suto */
  /* overflow-y-scroll overflow-x-visible */;
}

.menuUnpinned {
  @apply fixed w-80 left-20 h-full m-0 bg-gray-800 shadow-2xl duration-300 ease-in-out z-sideBarMenu 
  /* overflow-y-scroll */;
}

.hideMenu {
  @apply -left-80 z-sideBarMenu;
}

.title {
  @apply flex flex-col justify-center 
    m-0 p-2;
}

.titleText {
  @apply text-2xl tracking-wider font-bold 
    text-gray-400 
    mr-auto ml-4 my-auto align-middle;
}

.subTitle {
  @apply text-sm text-gray-400;
}

.subTitle::before {
  content: '## ';
}

.arrow {
  @apply my-auto mr-2 
     transition-all duration-100 ease-linear;
}

.arrowRotated {
  transform: rotate(90deg);
}

.menuSideBarDropdowns {
  @apply flex flex-col items-center justify-start 
    p-1 m-0;
}

.mainItemContainer {
  @apply flex w-full flex-row;
}

/* .container {
  @apply flex w-full flex-row;
} */

.subMenuItemContainer {
  @apply flex w-full flex-row justify-between items-center;
}

.dropdown {
  @apply m-0  px-2 pb-2
  flex-grow 
  text-gray-500;
}

.dropdownHeader {
  @apply flex flex-row
    mx-0 mt-2
    text-gray-500
    hover:text-gray-400
    transition duration-200 ease-in-out
    cursor-pointer;
}

.dropdownHeaderSelected {
  @apply text-gray-400;
}

.dropdownHeaderText {
  @apply text-lg font-semibold;
}

.dropdownPlus {
  @apply p-2 mt-2
  text-gray-500
  cursor-pointer;
}

.dropdownPlusSelected {
  @apply p-2 mt-2 bg-gray-400 rounded-full text-gray-800;
}

.dropdownPlus:hover {
  @apply bg-gray-600 rounded-full text-gray-400;
}

.subMenuItem {
  @apply flex flex-row items-center justify-evenly
    mt-2 ml-2
    transition-all duration-200 ease-in-out 
    hover:text-gray-400
    cursor-pointer;
}

.subMenuItemSelected {
  @apply flex flex-row items-center justify-evenly
    mt-2 ml-2
    text-gray-400;
}

.subMenuBullet {
  @apply w-1 h-1 mr-2 rounded bg-gray-500
  transition-all duration-200 ease-in-out;
}

.subMenuBulletSelected {
  @apply w-2 h-2 mr-2 rounded bg-gray-400;
}

.subMenuText {
  @apply font-semibold tracking-wide
    w-full;
}

.subMenuItemSelected {
  @apply text-gray-400;
}

.plusContainer {
  @apply h-min relative;
}

.tooltip {
  @apply absolute w-auto p-2 min-w-max rounded-md shadow-md
    text-white bg-gray-900
    text-xs font-bold 
    transition-all duration-100
    bottom-1/2 left-1/2  -translate-x-1/2 -translate-y-1/2
    origin-bottom z-toolTip;
}
