.container {
  @apply flex flex-col justify-between;
}

.fields {
  @apply flex flex-col flex-wrap items-start;
}

.actions {
  @apply flex justify-start rounded-lg py-2 shadow-sm;
}

/* .action {
  @apply ;
} */

.action button {
  @apply px-6 border-gray-600 border-2 hover:border-primary-600  hover:bg-primary-600 text-white mx-4 rounded;
}

.action button:disabled {
  @apply text-gray-500 hover:border-gray-600  hover:bg-transparent;
}

.reset {
  @apply text-red-400;
}

.divider {
  @apply bg-gray-600 w-full my-3;
  height: 2px;
}

.sectionTitle {
  @apply text-2xl mb-3;
}
