.horizontalLayout {
  @apply bg-gray-800 flex h-screen w-screen;
}

.verticalLayout {
  @apply flex flex-col m-0
  w-full h-full
  overflow-hidden
  bg-gray-700 z-content;
}

.topBar {
  @apply sticky top-0 z-topBar;
}

.content {
  /* TODO: padding right is missing */
  @apply h-full w-full overflow-x-auto;
  /* @apply h-full w-full overflow-x-scroll; */
}

.contentMargin {
  @apply m-3;
}
