.container {
  @apply h-screen w-20 flex flex-col  bg-gray-900 shadow-lg relative z-iconsMenu
  /* TODO: setting y to scroll causes x to be auto */
   /* overflow-x-visible overflow-y-scroll */;
}

.iconContainer {
  @apply flex flex-row-reverse;
}

.expanded {
  @apply flex-grow;
}

.icon {
  @apply relative flex items-center justify-center 
    h-12 w-12 mt-2 mb-2 mx-auto  
  bg-gray-800 
  text-primary-500  rounded-3xl  shadow-lg
  transition-all duration-300 ease-linear;
}

.icon:hover {
  @apply bg-primary-600 text-white rounded-2xl 
  transition-all duration-300 ease-linear
  cursor-pointer;
}

.iconSelected {
  @apply bg-primary-600 text-white rounded-2xl 
  transition-all duration-300 ease-linear;
}

.iconSideLine {
  @apply bg-transparent w-1 my-7 rounded-r-2xl
  transition-all duration-300 ease-linear;
}

.iconSideLineSelected {
  @apply bg-white w-1 my-4 rounded-r-2xl
  transition-all duration-300 ease-linear;
}

.tooltip {
  @apply z-toolTip
  absolute w-auto p-2 m-2 min-w-max left-14 rounded-md shadow-md
    text-white bg-gray-900 
    text-xs font-bold 
    transition-all duration-100 origin-left;
}

.hr {
  @apply bg-gray-800 
    border border-gray-600 rounded-full
    w-10 mx-auto;
}

.pinIcon {
  @apply relative flex items-center justify-center 
    h-10 w-10 mt-2 mb-2 mx-auto  
  bg-gray-800 
  text-white  rounded-full  shadow-lg;
}

.pinIcon:hover {
  @apply bg-primary-600 transition-all duration-300 ease-linear
  cursor-pointer;
}
