@import-normalize;
@tailwind base;
@tailwind components;
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  @apply text-gray-400;
  background: black;
}

@layer components {
}

@tailwind utilities;

::-webkit-scrollbar {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  @apply bg-primary-600 rounded-md;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-primary-500 rounded-md;
}

::-webkit-scrollbar-track {
  @apply bg-primary-800  rounded-md;
}

::-webkit-scrollbar-corner {
  @apply bg-transparent;
}

.prevent-text-selection {
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none;
}

.no-match {
  @apply flex flex-col justify-center items-center h-full pb-14;
}

.no-match h1 {
  @apply text-3xl text-center px-14;
}

.no-match h2 {
  @apply text-lg text-center px-14 py-2;
}
